/* Footer.css */
footer {
    background-color: #ffffff;
    color: #000000;
    padding: 10px;
    height: 70px;
    text-align: center;
}

ul {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
}

li {
    margin: 0 10px;
}

a {
    color: #000000;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}
